export default {
  props: {
    show: Boolean,
    message: String,
    event_handler: Function,
  },

  data: () => ({
    show_dialog: false,
  }),

  watch: {
    show(_, __) {
      if (_) {
        this.show_dialog = true;
      }
    },

    show_dialog(_, __) {
      if (!_) {
        this.event_handler("show_dialog", false);
        this.event_handler("show_dialog_message", "");
      }
    },
  },
}
